body {
  padding: 30px;
}
.PeopleForm {
  box-sizing: border-box;
  float: left;
  width: 50%;
  height: 50%;
  border: 1px solid black;

}
.evaluationGrid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.EvaluationGridLine {
  display: flex;
}

.EvaluationGridElement {
  border: 1px solid black;
}

.EvaluationGridLineID {
  flex: 0 0 50px;
}

.EvaluationGridLineQuestion {
  flex: 0 0 25%;
}

.EvaluationGridLinePt {
  flex: 0 0 50px;
}

.EvaluationGridLineJustification {
}

.EvaluationSignContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  /*border: 1px solid gray;*/
}
.EvaluationSignBlock {
  /*border: 1px solid gray;*/
  width: 30%;
  padding: 0px 30px;
}
.textLine {
  border-bottom: 0.1px solid gray;
  padding-top: 3em;
}
/* DivTable.com */
.divTable{
  display: table;
  width: 100%;
}
.divTableRow {
  display: table-row;
}
.divTableHeading {
  background-color: #EEE;
  display: table-header-group;
}
.divTableCell, .divTableHead {
  border: 1px solid #999999;
  display: table-cell;
  padding: 3px 10px;
}
.divTableHeading {
  background-color: #EEE;
  display: table-header-group;
  font-weight: bold;
}
.divTableFoot {
  background-color: #EEE;
  display: table-footer-group;
  font-weight: bold;
}
.divTableBody {
  display: table-row-group;
}
